import { Icon } from "@iconify/react";

export default function Footer(){
    return (
        <div className="p-2 w-full flex items-center justify-between px-4">
            <div>
            ®  {new Date().getFullYear()}. All Right Reserved
            </div>
            <div className="flex gap-2">
                <button className="btn-circle btn p-0 btn-error"><Icon color={'white'} icon = 'bxl:twitter' width={20}></Icon></button>
                <button className="btn-circle btn p-0 btn-error"><Icon color={'white'} icon = 'gg:facebook' width={20}></Icon></button>
                <button className="btn-circle btn p-0 btn-error"><Icon color={'white'} icon = 'fa:youtube' width={20}></Icon></button>
            </div>
        </div>
    )
}